.arrow {
  width: 15vw;
  max-width: 75px;
  height: 15vw;
  max-height: 75px;
  display: block;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.arrow.up {
  -webkit-transform: rotate(90deg) translateX(0px);
      -ms-transform: rotate(90deg) translateX(0px);
          transform: rotate(90deg) translateX(0px);
}

.arrow.down {
  -webkit-transform: rotate(-90deg) translateX(0px);
      -ms-transform: rotate(-90deg) translateX(0px);
          transform: rotate(-90deg) translateX(0px);
}

.arrow {
  border-radius: 8vh;
  -webkit-box-shadow: 0px 0px 15px 2px;
          box-shadow: 0px 0px 15px 2px;
}

.arrow path {
  fill: white;
}

.arrow:hover {
  cursor: pointer;
  color: antiquewhite;
}

.arrow.up:hover {
  -webkit-transform: rotate(90deg) translateX(-10px);
      -ms-transform: rotate(90deg) translateX(-10px);
          transform: rotate(90deg) translateX(-10px);
}

.arrow.down:hover {
  -webkit-transform: rotate(-90deg) translateX(-10px);
      -ms-transform: rotate(-90deg) translateX(-10px);
          transform: rotate(-90deg) translateX(-10px);
}
.arrow:hover * {
  -webkit-box-shadow: 0px 0px 20px 4px;
          box-shadow: 0px 0px 20px 4px;
}



