.double-chevron {
  height: 14px;
}

.double-chevron.up {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}

.double-chevron.down {
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.double-chevron.padded.up {
  margin-bottom: 10px;}

.double-chevron.padded.down {
  margin-top: 10px;
}

.double-chevron path:nth-child(1) {
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-name: swipeme_a;
          animation-name: swipeme_a;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.double-chevron path:nth-child(2) {
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-name: swipeme_b;
          animation-name: swipeme_b;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes swipeme_a {
  0%   { fill: lightgray; }
  50%  { fill: black; }
  100% { fill: lightgray; }
}

@keyframes swipeme_a {
  0%   { fill: lightgray; }
  50%  { fill: black; }
  100% { fill: lightgray; }
}

@-webkit-keyframes swipeme_b {
  0%   { fill: black; }
  50%  { fill: lightgray; }
  100% { fill: black; }
}

@keyframes swipeme_b {
  0%   { fill: black; }
  50%  { fill: lightgray; }
  100% { fill: black; }
}



